header {
    margin-top: 3rem;
}

.chip-header {
    background-color: var(--bgSecondHelper) !important;
}

.title {
    cursor: pointer;
}

.block {
    background-color: var(--bgBody) !important;
    border-radius: 10px;
    padding: 20px 40px;
}

.last_match_fastCard {
    width: 200px;
    height: 193px; 
    background-color: var(--bgSecondHelper);
    border-radius: 10px;
    padding: 10px 20px;
}


.loser {
    font-size: 2em !important;
    color: var(--helpColor) !important;
}
.winner {
    color: var(--colorAction) !important;
}
.not_we {
    color: var(--colorActionSecond) !important;
}

.date_fastCard {
    margin-top: -7px !important;
}

.greenColor, .redColor {
    width: 30%;
    height: 5px;
}
.greenColor {
    background-color: var(--colorAction) !important;
}
.redColor {
    background-color: var(--colorActionSecond) !important;
}


.lastGames-el {
    width: 15px;
    height: 15px;
    border-radius: 7.5px;
    background-color: var(--helpColor) !important;
}
.LS-win {
    background-color: var(--colorActionSecond) !important;
}
.LS-lose {
    background-color: var(--colorAction) !important;
}

.headerChips .MuiChip-root {
    padding-left: 5px !important;
}

.goals .winner {
    font-weight: 500 !important;
}

.player-card {
    cursor: pointer;
    background-color: var(--bgSecondHelper) !important;
    border: 2px solid transparent;
    border-radius: 10px;
    padding: 10px 20px;
}
.player-card:hover {
    border: 2px solid var(--colorAction);
}

.actionColor {
    color: var(--colorAction) !important;
}
.actionColorSecond {
    color: var(--colorActionSecond) !important;
}

.teammat {
    width: 20%;
}

.newsCard {
    cursor: pointer;
    border: 2px solid transparent;
}
.newsCard:hover {
    border: 2px solid var(--colorAction);
}

.white path {
    color: var(--colorWhite) !important;
}
